<template>
  <div class="category-table">
    <table class="showTable">
      <tr>
        <th v-for="th in ths" :key="th">{{th}}</th>
      </tr>

      <tr :key="data.id" v-for="(data,i) in allCategory">
        <td>{{data.id}}</td>
        <td>{{data.name}}</td>
        <td>
          <img :src="data.img" alt />
        </td>
        <td>{{data.remark}}</td>
        <td>
          <el-button type="success" round @click="edit(i)">修改</el-button>
          <el-button type="danger" round @click="del(i)">删除</el-button>
        </td>
      </tr>
    </table>

    <el-dialog title="分类信息修改" :visible.sync="dialogVisible" width="50%">
      <view-form
        :labelW="'100px'"
        :needInfo="showCurCategoryInfo"
        :defaultImg="showCurCategoryInfo.img?showCurCategoryInfo.img.originVal:''"
        ref="formData"
        @changeShow="dialogVisible = $event"
        :showSubmit="false"
      />
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeVisible(false)">取 消</el-button>
        <el-button type="primary" @click="confirmChange">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import ViewForm from "./ViewForm";
import { mapMutations, mapState } from "vuex";

export default {
  components: {
    ViewForm
  },
  data() {
    return {
      dialogVisible: false,
      // personInfo: {},
      showCurCategoryInfo: {},
      categoryIndex: null
    };
  },
  props: {
    tableData: {
      type: Array,
      default() {
        return [];
      }
    },
    ths: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState("category", ["allCategory", "curCategoryInfo"])
  },
  methods: {
    ...mapMutations("category", [
      "deleteCategory",
      "changeOneCategory",
      "changeCurCategoryInfo"
    ]),
    del(i) {
      this.$confirm("确定删除该信息嘛", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          /* 删除的api */
          this.deleteCategory(i);
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    edit(i) {
      this.categoryIndex = i;
      let tempCur = {};
      for (let prop in this.ths) {
        tempCur[prop] = this.allCategory[i][prop];
      }
      this.changeCurCategoryInfo(tempCur);
      this.changeVisible(true);

      let tempCurDetail = {};
      for (let prop in this.ths) {
        if (prop !== "operate") {
          tempCurDetail[prop] = {
            labelName: this.ths[prop],
            originVal: this.allCategory[i][prop]
          };
        }
        if (prop === "img") {
          tempCurDetail[prop] = {
            labelName: this.ths[prop],
            originVal: this.allCategory[i][prop],
            type: "uploadImg"
          };
        }
      }
      this.showCurCategoryInfo = tempCurDetail;
      this.$nextTick(() => {
        this.$refs.formData.updateAlertInfo();
      });
    },
    confirmChange() {
      /* 更新的api */
      let i = this.categoryIndex;
      let val = this.$refs.formData.infoVals;
      this.changeOneCategory({ i, val });

      this.changeVisible(false);
    },
    changeVisible(status) {
      this.dialogVisible = status;
    }
  }
};
</script>

<style>
.category-table .showTable {
  width: 100%;
  border-collapse: collapse;
  text-align: center;
}
.showTable th {
  background-color: rgb(212, 245, 245);
  padding: 10px 0;
  border: 1px solid #ccf;
}
.showTable td {
  padding: 10px 0;
  border: 1px solid #ccf;
}
.showTable img {
  width: 200px;
}
</style>