<template>
  <div class="upload-img" @click="handleClick">
    <i class="addImg"></i>
    <img :src="defaultImg?defaultImg:''" alt class="preImg" />
    <input type="file" accept="image/*" class="uploadInp" @change="changeImg" />
  </div>
</template>

<script>
export default {
  props: {
    imgW: {
      type: String,
      default: "200px"
    },
    imgH: {
      type: String,
      default: "200px"
    },
    defaultImg: {
      type: String
    }
  },
  computed: {
    uploadDiv() {
      return this.$el;
    },
    uploadInp() {
      return this.$el.querySelector(".uploadInp");
    },
    addImg() {
      return this.$el.querySelector(".addImg");
    },
    preImg() {
      return this.$el.querySelector(".preImg");
    }
  },
  created() {
    console.log(this);
    this.$nextTick(() => {
      if (this.defaultImg) {
        this.addImg.style.display = "none";
        this.uploadDiv.style.border = "none";
      }
      this.uploadDiv.style.width = this.imgW;
      this.uploadDiv.style.height = this.imgH;
    });
  },

  methods: {
    handleClick() {
      this.uploadInp.click();
    },
    changeImg() {
      let fileReader = new FileReader();
      fileReader.readAsDataURL(this.uploadInp.files[0]);
      fileReader.onload = () => {
        this.$emit("changeImg", fileReader.result);
        this.preImg.src = fileReader.result;
        this.preImg.onload = () => {
          this.addImg.style.display = "none";
          this.uploadDiv.style.border = "none";
        };
      };
    }
  }
};
</script>

<style scoped>
.upload-img {
  position: relative;
  border: 1px dashed #000;
}
.upload-img:hover {
  border-color: #409eff;
}
.addImg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  font-family: "iconfont";
  font-style: normal;
}
.addImg::before {
  content: "\e61c";
}
.upload-img {
  position: relative;
}
.uploadInp {
  display: none;
}
.preImg {
  width: 100%;
  height: 100%;
}
</style>