<template>
  <div class="selectImg">
    <div class="s-btn-box">
      <label for="select-btn">点击选择更多图片</label>
      <input type="file" multiple id="select-btn" accept="image/*" @change="chooseImg" />
    </div>
    <ul class="img-box" v-if="urls.length>0">
      <li
        v-for="(url,i) in urls"
        :key="`${i}${url}`"
        @mouseenter="hideRemoveBtn=false"
        @mouseleave="hideRemoveBtn=true"
        :class="[i===0?'defaultImg':'']"
      >
        <img :src="url" alt />
        <span :class="['remove-btn',hideRemoveBtn?'hide':'']" @click="deleteImg(i)">删除</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      urls: [],
      selectBtn: null,
      hideRemoveBtn: true
    };
  },
  mounted() {
    this.selectBtn = document.querySelector("#select-btn");
  },
  methods: {
    chooseImg() {
      for (let i = 0; i < this.selectBtn.files.length; i++) {
        let reader = new FileReader();
        reader.readAsDataURL(this.selectBtn.files[i]);
        reader.onload = () => {
          if (!this._hasUrl(reader.result)) {
            this.urls.push(reader.result);
          }
        };
      }
      this.$emit("input", this.urls);
    },
    deleteImg(i) {
      this.urls.splice(i, 1);
    },
    _hasUrl(result) {
      let existUrl = this.urls.some(function(url, index) {
        return url === result;
      });
      return existUrl;
    }
  }
};
</script>

<style scoped>
.selectImg {
  /* width: 600px; */
  width: 100%;
  border: 1px solid #bbb;
  /* box-sizing: border-box; */
  /* padding: 10px; */
  /* margin: 100px auto; */
}

.selectImg .s-btn-box {
  width: 100%;
  height: 20px;
  border-radius: 5px;
  /* border: 1px solid skyblue; */
  text-align: center;
  font-size: 15px;
  line-height: 20px;
  color: skyblue;
  background: none;
  position: relative;
  padding: 20px 0;
}

.selectImg .s-btn-box label {
  width: 100%;
  height: 100%;
  display: block;
  cursor: pointer;
  font-size: 25px;
}

.selectImg .s-btn-box #select-btn {
  width: 100%;
  height: 100%;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
}

.selectImg .img-box {
  width: 100%;
  display: flex;
  /* justify-content: space-around; */
  flex-wrap: wrap;
}

.selectImg .img-box::after {
  content: "";
  display: block;
  clear: both;
}

.selectImg .img-box li {
  width: 150px;
  height: 100px;
  /* float: left; */
  margin: 5px;
  position: relative;
  overflow: hidden;
}
.selectImg .img-box li.defaultImg::after {
  content: "默认图片";
  position: absolute;
  background-color: #f40;
  color: #fff;
  right: -7px;
  top: 8px;
  transform: rotate(45deg);
  font-size: 13px;
  line-height: 22px;
}

.img-box li .remove-btn {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  color: #fff;
  cursor: pointer;
  line-height: initial;
}
.img-box li .remove-btn.hide {
  display: none;
}
.img-box li img {
  width: 100%;
  height: 100%;
}
</style>