<template>
  <div class="directory">
    <div class="directory-detail">
      <i class="icon-home"></i>
      <span v-for="(name,i) in curDir" :key="name">
        <span class="dir-name">{{name}}</span>
        <span v-if="i !== curDir.length-1">&gt;</span>
      </span>
    </div>
    <div class="refresh">
      <el-button type="success" icon="el-icon-refresh" size="large" @click="refresh"></el-button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "ViewHeader",
  computed: {
    ...mapState(["curDir"])
  },

  methods: {
    refresh() {
      this.$emit("refresh");
    }
  }
};
</script>

<style scoped>
.icon-home {
  font-size: 20px;
  font-family: "iconfont";
  font-style: normal;
}
.icon-home::before {
  content: "\e60f";
}

.directory {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 10px; */
  height: 50px;
  padding: 0 10px;
  background-color: #ddd;
}

.dir-name {
  padding: 0 10px;
  font-weight: 700;
}
</style>