import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import Test from '../views/Test.vue';
import createConfigure from '../views/config/createConfigure.vue';
import createCategory from '../views/category/createCategory.vue';
import manageCategory from '../views/category/manageCategory.vue';
import createCommodity from '../views/commodity/createCommodity.vue';
import manageCommodity from '../views/commodity/manageCommodity.vue';

console.log(Test);


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    children: [
      {
        path: 'createConfigure',
        name: 'createConfigure',
        component: createConfigure
      },
      {
        path: 'createCategory',
        name: 'createCategory',
        component: createCategory
      },
      {
        path: 'manageCategory',
        name: 'manageCategory',
        component: manageCategory
      },
      {
        path: 'createCommodity',
        name: 'createCommodity',
        component: createCommodity
      },
      {
        path: 'manageCommodity',
        name: 'manageCommodity',
        component: manageCommodity
      },
      {
        path: 'test',
        name: 'test',
        component: Test
      },

    ]
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
