<template>
  <div class="search-commodity">
    <div class="searchBox" id="searchCommodity">
      <div class="search-area">
        <input
          type="text"
          id="searchText"
          placeholder="产品名称"
          :value="searchValue"
          @input="getContent($event.target.value)"
        />
        <el-button type="primary" icon="el-icon-search" class="searchBtn" @click="search">搜索</el-button>
      </div>

      <ul class="dataBox" id="dataBox" v-if="showSearchArr.length > 0 && clickHideData">
        <li
          v-for="(data,i) in showSearchArr"
          :key="`${data}${i}${new Date()}`"
          @click.stop="hideDataBox(data)"
        >{{data.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";
export default {
  mounted() {
    document.addEventListener("click", () => {
      this.hideDataBox();
    });
  },
  data() {
    return {
      searchValue: "",
      showSearchArr: [],
      // debounceKey: false,
      timer: null,
      clickHideData: true
    };
  },
  methods: {
    ...mapActions("commodity", ["searchCommodity"]),
    ...mapMutations("commodity", ["changeIsLoading"]),
    getContent(val) {
      this.clickHideData = true;
      this.searchValue = val;
      if (this.timer) {
        clearTimeout(this.timer);
      }
      // console.log(111);
      this.timer = setTimeout(() => {
        console.log(this.searchValue);
        if (this.searchValue) {
          this.getVal(this.searchValue).then(val => {
            console.log(this.searchValue);
            console.log("=======");
            console.log(val);
            this.showSearchArr = val;
          });
        } else {
          this.showSearchArr = [];
        }
      }, 500);
    },
    getVal(val) {
      /* 关键字搜索api */
      "=====================";
      /* mock */
      return new Promise(resolve => {
        setTimeout(() => {
          this.showSearchArr = this.$mockData.allCommodity.filter(result => {
            if (result.name.indexOf(val) !== -1) {
              return true;
            }
          });
        }, 300);
      });
    },
    hideDataBox(dataObj) {
      if (dataObj) {
        this.searchValue = dataObj.name;
      }
      this.showSearchArr = [];
    },
    search() {
      // console.log(this.searchValue);
      this.changeIsLoading(true);
      this.searchCommodity(this.searchValue).then(() => {
        this.changeIsLoading(false);
      });
    }
  }
};
</script>

<style>
.searchBtn {
  height: 100%;
}

.searchBox {
  border: 1px solid;
  width: 500px;
  height: 50px;
  margin: 20px auto;
  box-sizing: border-box;
  position: relative;
}
.search-area {
  display: flex;
  height: 100%;
}
.searchBox input {
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  padding: 0px 50px 0px 10px;
  font-size: 20px;
  box-sizing: border-box;
  /* height: 30px; */
  /* line-height: 30px; */
  /* border: none; */
  /* padding-left: 2px; */
}
.searchBox ul {
  width: 100%;
  position: absolute;
  left: 0;
  border: 1px solid #000;
}

.searchBox ul li {
  cursor: pointer;
  padding: 10px 0;
  background-color: #fff;
}
.searchBox ul li:hover {
  background: #bbb;
}
</style>