import $mockData from '@/mock/mock.js';

export default {
    namespaced: true,
    state: {
        allCommodity: [],
        showCommodity: [],
        curCommodity: {},
        isLoading: false
    },
    mutations: {
        changeShowCommodity(state, val) {
            state.showCommodity = val;
        },
        changeOneShowCommodity(state, { i, val }) {
            for (const prop in val) {
                state.showCommodity[i][prop] = val[prop];
            };
            console.log(state.allCommodity)
        },
        deleteOneShowCommodity(state, i) {
            let j = state.allCommodity.findIndex((com) => com === state.showCommodity[i]);
            state.allCommodity.splice(j, 1);
            if (state.allCommodity !== state.showCommodity) {
                state.showCommodity.splice(i, 1);
            }
        },
        changeAllCommodity(state, val) {
            state.allCommodity = val;
        },
        // changeOneCommodity(state, { i, val }) {
        //     for (const prop in val) {
        //         state.allCommodity[i][prop] = val[prop];
        //     };
        //     state.curCommodity = {};
        // },
        changeCurCommodity(state, i) {
            state.curCommodity = state.showCommodity[i];
        },
        resetCurCommodity(state) {
            state.curCommodity = {}
        },
        // deleteOneCommodity(state, i) {
        //     state.allCommodity.splice(i, 1);
        // },

        createOneCommodity(state, info) {
            state.allCommodity.unshift(info);
        },
        changeIsLoading(state, status) {
            state.isLoading = status;
        }
    },
    actions: {
        getAllCommodity({ commit }, val) {
            commit('changeIsLoading', true);
            return new Promise((resolve) => {
                setTimeout(() => {
                    commit('changeAllCommodity', val ? val : $mockData.allCommodity);
                    commit('changeShowCommodity', val ? val : $mockData.allCommodity);
                    commit('changeIsLoading', false);
                    resolve();
                }, 1000)
            })
        },
        updateOneShowCommodity({ commit }, info) {
            commit('changeIsLoading', true);
            return new Promise((resolve) => {
                setTimeout(() => {
                    commit('changeOneShowCommodity', info);
                    // commit('changeOneCommodity', info);
                    commit('changeIsLoading', false);
                    resolve();
                }, 500)
            })
        },
        deleteOneShowCommodity({ commit }, i) {
            commit('changeIsLoading', true);
            return new Promise((resolve) => {
                setTimeout(() => {
                    commit('deleteOneShowCommodity', i);
                    // commit('deleteOneCommodity', i);
                    commit('changeIsLoading', false);
                    resolve();
                }, 500)
            })
        },
        createOneCommodity({ commit, state, dispatch }, info) {
            commit('changeIsLoading', true);
            return new Promise((resolve) => {
                setTimeout(() => {
                    if (state.allCommodity.length === 0) {
                        dispatch('getAllCommodity').then(() => {
                            commit('createOneCommodity', info);
                            commit('changeIsLoading', false);
                            resolve();
                        });
                    } else {
                        commit('createOneCommodity', info);
                        commit('changeIsLoading', false);
                        resolve();
                    }

                }, 500)
            })
        },
        searchCommodity({ commit, state }, val) {
            commit('changeIsLoading', true);
            return new Promise((resolve) => {
                setTimeout(() => {

                    let info;
                    if (val) {
                        info = state.allCommodity.filter((com) => com.name.indexOf(val) != -1)
                    } else {
                        info = state.allCommodity;
                    }
                    console.log(info);
                    // console.log()
                    commit('changeShowCommodity', info);
                    commit('changeIsLoading', false);
                    resolve();
                }, 500)
            })
        }

    }
}