<template>
  <div class="manage-category" v-loading="loading">
    <view-header @refresh="refresh" />
    <category-table v-if="allCategory.length !== 0" :tableData="allCategory" :ths="th" />
  </div>
</template>
<script>
// import ViewHeader from "@/components/ViewHeader";
import CategoryTable from "@/components/CategoryTable";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  components: {
    // ViewHeader,
    CategoryTable
  },
  created() {
    this.getNewData(); /* 刚开始获取数据 */
  },
  computed: {
    ...mapState("category", ["allCategory"])
  },
  methods: {
    ...mapActions("category", ["getAllCategory"]),
    getNewData() {
      this.loading = true;
      this.getAllCategory().then(() => {
        this.loading = false;
      });
    },
    refresh() {
      this.getNewData();
    }
  },

  data() {
    return {
      th: {
        id: "ID",
        name: "名称",
        img: "图片",
        remark: "备注",
        operate: "操作"
      },
      loading: true
    };
  }
};
</script>

  




