import Vue from 'vue'
import Vuex from 'vuex'
import $mockData from '@/mock/mock.js';
import category from './category.js';
import configure from './configure.js';
import commodity from './commodity.js';
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    curDir: [],/* 当前路径，如：['首页','xx','xx'] */
  },
  mutations: {
    changeCurDir(state, dir) {
      state.curDir = ['首页'].concat(dir);
    },
  },
  modules: {
    configure,
    category,
    commodity
  }
})
