<template>
  <div class="form-area">
    <el-form
      :model="infoVals"
      :rules="rules"
      ref="infoVals"
      :label-width="labelW"
      label-position="left"
    >
      <!-- <el-form-item label="活动名称" prop="name">
      <el-input v-model="infoForm.name"></el-input>
    </el-form-item>
    <el-form-item label="作者" prop="name">
      <el-input v-model="infoForm.name"></el-input>
      </el-form-item>-->

      <template v-for="info in infoForm">
        <el-form-item :label="info.labelName" :prop="info.propName" :key="info.propName">
          <upload-img
            v-if="info.type==='uploadImg'"
            :imgW="info.w"
            :imgH="info.h"
            :defaultImg="defaultImg?defaultImg:''"
            @changeImg="changeImg(info.propName,$event)"
            ref="uploadImg"
          />

          <el-input
            v-else
            :type="info.type==='textarea'?info.type:''"
            v-model="infoVals[info.propName]"
          ></el-input>
        </el-form-item>
      </template>

      <el-form-item v-if="showSubmit">
        <el-button type="primary" @click="submitForm">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import UploadImg from "./uploadImg";
import { mapState, mapMutations } from "vuex";
export default {
  components: {
    UploadImg
  },
  created() {
    this.updateAlertInfo();
  },
  props: {
    showSubmit: {
      type: Boolean,
      default: true
    },
    needInfo: {
      type: Object,
      default() {
        return {};
      }
    },
    needRules: {
      type: Object,
      default() {
        return {};
      }
    },
    labelW: {
      type: String,
      default: "300px"
    },
    defaultImg: {
      type: String
    },
    sumbitOption: {
      type: Object
    }
  },
  data() {
    /* infoVals */
    let infoVals = {};
    for (let key in this.needInfo) {
      infoVals[key] = "";
    }
    return {
      infoVals,
      infoForm: [],
      rules: this.needRules
    };
  },
  methods: {
    ...mapMutations("category", ["createNewCategory"]),
    submitForm() {
      this.$refs.infoVals.validate(valid => {
        if (valid) {
          if (this.sumbitOption) {
            if (this.sumbitOption.desc === "createNewCategory") {
              this.createNewCategory(this.infoVals);
            }
          }
          // alert("添加成功");
          this.$message({
            message: "添加成功",
            type: "success"
          });
        } else {
          return false;
        }
      });
    },
    changeImg(key, val) {
      this.infoVals[key] = val;
    },
    /* 弹出框的数据 */
    updateAlertInfo() {
      /* infoForm */
      this.infoForm = [];

      for (let key in this.needInfo) {
        if (typeof this.needInfo[key] === "object") {
          if (this.needInfo[key].originVal) {
            this.infoVals[key] = this.needInfo[key].originVal;
          }
          this.infoForm.push({ ...this.needInfo[key], propName: key });
        } else {
          this.infoForm.push({
            labelName: this.needInfo[key],
            propName: key
          });
        }
      }
    }
  }
};
</script>

<style>
.form-area {
  padding: 30px 30px 0 30px;
  box-sizing: border-box;
}
.el-form label {
  font-weight: bold;
  font-size: 16px;
}
.el-textarea__inner {
  min-height: 100px !important;
  resize: none;
}
</style>