<template>
  <div class="commodity-form">
    <el-form :model="commodityInfo" ref="infoVals" :label-width="labelW" label-position="left">
      <!-- <el-form-item label="活动名称" prop="name">
      <el-input v-model="infoForm.name"></el-input>
    </el-form-item>
    <el-form-item label="作者" prop="name">
      <el-input v-model="infoForm.name"></el-input>
      </el-form-item>-->

      <el-form-item label="商品名称" prop="name">
        <el-input v-model="commodityInfo.name" placeholder="产品名称"></el-input>
      </el-form-item>
      <el-form-item label="商品全称" prop="fullName">
        <el-input v-model="commodityInfo.fullName" placeholder="广告语"></el-input>
      </el-form-item>
      <el-form-item label="所属分类" prop="category">
        <el-cascader :options="categories" v-model="commodityInfo.category" @change="handleChange"></el-cascader>
      </el-form-item>
      <el-form-item label="商品展示图组" prop="images">
        <upload-multiple-img v-model="commodityInfo.images" />
      </el-form-item>
      <el-form-item label="商品介绍" prop="content">
        <vue-ueditor v-model="commodityInfo.descMsg" :config="ueditorConfig"></vue-ueditor>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapMutations, mapActions, mapGetters } from "vuex";
import uploadMultipleImg from "./uploadMultipleImg.vue";
export default {
  components: {
    uploadMultipleImg
  },
  props: {
    labelW: {
      type: String,
      default: "300px"
    }
  },
  data() {
    return {
      ueditorConfig: {
        lang: "zh-cn",
        serverUrl: "",
        autoClearinitialContent: false,
        autoHeightEnabled: false,
        initialFrameHeight: 200,
        initialFrameWidth: "100%"
      },
      commodityInfo: {
        name: "",
        fullName: "",
        category: "",
        images: "",
        descMsg: ""
      }
    };
  },
  computed: {
    ...mapGetters("category", ["categoryName"]),
    categories() {
      /* 
        categories: [
          {
            value: '首饰',
            label: '首饰'
          },
          ....
        ]
      */
      let categories = [];
      this.categoryName.forEach(name => {
        categories.push({ value: name, label: name });
      });
      return categories;
    }
  },
  methods: {
    ...mapActions("commodity", ["createOneCommodity"]),
    handleChange(selectCategory) {
      this.commodityInfo.category = selectCategory[0];
      console.log(this.commodityInfo.category);
    },
    submitForm() {
      console.log(this.commodityInfo);
      // console.log(this.commodityInfo);
      let info = {
        ...this.commodityInfo,
        recommend: true,
        img: this.commodityInfo.images[0],
        id: Math.random()
      };
      // console.log(info);
      this.createOneCommodity(info).then(() => {
        this.$message({
          type: "success",
          message: "添加成功"
        });
        this.resetForm();
      });
    },
    resetForm() {
      this.commodityInfo = {
        name: "",
        fullName: "",
        category: "",
        images: "",
        content: ""
      };
    }
  }
};
</script>

<style scoped>
.commodity-form {
  padding: 30px 30px 0 30px;
}
</style>

