<template>
  <div class="commodity-table">
    <div class="emptyData" v-if="showCommodity.length === 0">
      <el-button type="primary" round @click="createCommodityBtn">添加商品</el-button>
    </div>
    <table class="comTableList" v-else>
      <tr>
        <th v-for="th in ths" :key="th">{{th}}</th>
      </tr>
      <tr :key="data.id" v-for="(data,i) in showCommodity">
        <td>{{data.id}}</td>
        <td>
          <img :src="data.img" alt />
        </td>
        <td>{{data.name}}</td>
        <td :style="{color: data.recommend?'#0f0':'#f40'}">{{data.recommend?'是':'否'}}</td>
        <td>
          <el-button type="info" round>规格</el-button>
          <el-button type="warning" round @click="editCurCommodity(i)">修改</el-button>
          <el-button type="primary" round>推荐</el-button>
          <el-button type="danger" round @click="deleteCurCommodity(i)">删除</el-button>
        </td>
      </tr>
    </table>

    <el-dialog width="50%" title="商品信息修改" :visible.sync="dialogVisible">
      <el-form :model="commodityResult" label-width="300px" label-position="left">
        <el-form-item :label="ths.id" prop="id">
          <el-input v-model="commodityResult.id"></el-input>
        </el-form-item>
        <el-form-item :label="ths.img" prop="img">
          <upload-img :defaultImg="curCommodity.img" @changeImg="changeImg($event)" />
        </el-form-item>
        <el-form-item :label="ths.name" prop="name">
          <el-input v-model="commodityResult.name"></el-input>
        </el-form-item>
        <el-form-item :label="ths.recommend" prop="recommend">
          <el-switch
            :value="commodityResult.recommend === 'init'?curCommodity.recommend:commodityResult.recommend"
            @change="changeRecommend"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetCommodity">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapMutations, mapState, mapActions } from "vuex";
import uploadImg from "@/components/uploadImg.vue";
// :value="commodityResult.recommend==='init'?curCommodity.recommend:commodityResult.recommend"
export default {
  components: {
    uploadImg
  },
  mounted() {
    this.getAllCommodity();
  },
  data() {
    return {
      ths: {
        id: "ID",
        img: "图片",
        name: "产品名称",
        recommend: "推荐",
        operations: "操作"
      },
      commodityResult: {
        id: "",
        img: "",
        name: "",
        recommend: "init"
      },
      dialogVisible: false,
      curIndex: null
    };
  },
  computed: {
    ...mapState("commodity", ["allCommodity", "curCommodity", "showCommodity"])
  },
  methods: {
    ...mapMutations("commodity", [
      // "changeAllCommodity",
      // "changeOneCommodity",
      "changeCurCommodity",
      "resetCurCommodity",
      "changeIsLoading"
    ]),
    ...mapActions("commodity", [
      "getAllCommodity",
      "updateOneShowCommodity",
      "deleteOneShowCommodity"
    ]),
    editCurCommodity(i) {
      this.curIndex = i;
      this.resetCommodity();
      this.changeCurCommodity(i);
      console.log(this.curCommodity);
      for (let prop in this.commodityResult) {
        this.commodityResult[prop] = this.curCommodity[prop];
      }
      this.changeVisible(true);
    },
    deleteCurCommodity(i) {
      this.$confirm("确定删除该商品信息嘛", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          /* 删除的api */

          this.deleteOneShowCommodity(i).then(() => {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    /*  */
    changeImg(imgUrl) {
      this.commodityResult.img = imgUrl;
    },
    /* 是否展示表单弹出框 */
    changeVisible(status) {
      this.dialogVisible = status;
    },
    /* 改变推荐按钮的布尔值 */
    changeRecommend(status) {
      console.log(status);
      this.commodityResult.recommend = status;
    },
    /* 重置弹出的表单数据 */
    resetCommodity() {
      this.commodityResult = {
        id: "",
        img: "",
        name: "",
        recommend: "init"
      };

      // this.changeCurCommodity({});
      this.resetCurCommodity();
      this.changeVisible(false);
    },
    /* 确认修改数据按钮 */
    submit() {
      console.log(this.commodityResult);
      let updateObj = {
        i: this.curIndex,
        val: this.commodityResult
      };
      this.updateOneShowCommodity(updateObj).then(() => {
        this.dialogVisible = false;
      });
    },
    /* 查无数据时显示的添加按钮 */
    createCommodityBtn() {
      if (process.env.NODE_ENV === "development") {
        this.$router.push("/createCommodity");
      } else {
        this.$router.push("/dist/createCommodity");
      }
    }
  }
};
</script>

<style scoped>
/* .comTableList {
  width: 100%;
  border-collapse: collapse;
  text-align: center;
}

.comTableList th,
.comTableList td {
  padding: 5px 10px;
  border: 1px solid #ccf;
}

.comTableList th {
  background-color: rgb(212, 245, 245);
} */
.emptyData {
  text-align: center;
}
.comTableList {
  width: 100%;
  border-collapse: collapse;
  text-align: center;
}
.comTableList th {
  background-color: rgb(212, 245, 245);
  padding: 10px 0;
  border: 1px solid #ccf;
}
.comTableList td {
  padding: 10px 0;
  border: 1px solid #ccf;
}
.comTableList img {
  width: 150px;
  height: 150px;
}
</style>