import $mockData from '@/mock/mock.js';
export default {
    namespaced: true,
    state: {
        curCategoryInfo: {},/* 编辑的当前的信息，如：{img: 'xxxx',name: 'xxx'} */
        allCategory: $mockData.allCategory,/* 总数据，看mock.js */
    },
    getters: {
        categoryName(state) {
            let categoryName = [];
            state.allCategory.forEach((category) => {
                categoryName.push(category.name);
            })
            console.log(categoryName)
            return categoryName;
        }
    },
    mutations: {
        changeCurCategoryInfo(state, val) {
            state.curCategoryInfo = val;
        },
        changeAllCategory(state, val) {
            state.allCategory = val;
        },
        changeOneCategory(state, { i, val }) {
            for (const prop in val) {
                state.allCategory[i][prop] = val[prop];
            };
            state.curCategoryInfo = {};
        },
        createNewCategory(state, val) {
            state.allCategory.unshift(val);
        },
        deleteCategory(state, i) {
            state.allCategory.splice(i, 1);
        },
    },
    actions: {
        getAllCategory({ commit }) {
            /* 获取的api */
            return new Promise((resolve) => {
                setTimeout(() => {

                    resolve();
                }, 1000)

            })
        },

    },
}